import React from "react"
import "./style.css"
import translate from "../../translate"
import classnames from "classnames"
import LoadingButton from "../../elements/loadingbutton";
import Button from "../../elements/button/button";
import {Helmet} from "react-helmet/es/Helmet";
import Seperator from "../../elements/seperator/seperator";


const ContactForm = ({className, locale}) => (


    <form onSubmit={(e) => {
        window.gtag('event', 'conversion')
    }} acceptCharset="UTF-8"
          action="https://getform.io/f/50a0ebfd-0647-4a2f-82ab-451fda4012fd" method="POST"
          encType="multipart/form-data" target="_blank" className={"contactform"}>
        <Helmet>
            <script src={`https://www.google.com/recaptcha/api.js`} async defer/>
        </Helmet>
        <input type="text" required={true} placeholder={"Name"} name="name"
               className={"contactinput"}/>
        <input type={"email"} required={true} name={"email"}
               placeholder={"E-Mail"} className={"contactinput"}/>
        <textarea placeholder={"Nachricht"} name="nachricht" rows={3} className={"contactinput"}/>
        <Button tagName={"button"} type={"submit"}>Anfragen</Button>
    </form>
)

export default ContactForm
